import { TaxaEntregaDTO } from './../../../../models/domain/taxa-entrega.dto';
import { TaxaEntregaService } from './../../config/taxaentrega/taxa-entrega.service';
import { FormaPagamentoService } from './../../config/formapagamento/forma-pagamento.service';
import { FormaPagamentoDTO } from './../../../../models/domain/forma-pagamennto.dto';
import { EnderecoDTO } from './../../../../models/domain/endereco.dto';
import { AbstractBaseComponent } from 'config/core/abstract-base.component';
import { TrataErrorService } from 'services/trata-error.service';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Location } from '@angular/common';

import { PedidoItemDTO } from './../../../../models/domain/pedido-item.dto';
import { ClienteDTO } from './../../../../models/domain/cliente.dto';

@Component({
  templateUrl: './create-pedido.component.html',
  styleUrls: ['./create-pedido.component.scss']
})
export class CreatePedidoComponent extends AbstractBaseComponent implements OnInit {


  cliente: any;
  pedidoItens: any[] = [];
  enderecos: any[];
  exibirProduto: boolean = false;
  vlTaxaEntrega: number = 0;
  subtotal: number = 0;
  formaPagmentos: FormaPagamentoDTO[] = [];
  taxaEntregas: TaxaEntregaDTO[] = [];

  constructor(
    public fb: FormBuilder,
    public pagService: FormaPagamentoService,
    public taxaService: TaxaEntregaService,
    public location: Location,
    public router: Router) {
    super();
  }

  ngOnInit() {
    this.criarFormulario();
    this.carregarCombos();
    this.produtofake();
    this.clientefake();
  }

  selecionarCliente(cliente: ClienteDTO) {
    this.exibirProduto = true;
    this.formulario.patchValue({
      cliente: {
        id: cliente.id,
        nmPessoa: cliente.nmPessoa
      }
    })
    this.pedidoItens = [];
    this.enderecos = cliente.enderecos;
  }

  selecionarProduto(pedidoItem: PedidoItemDTO) {
    this.pedidoItens.push(pedidoItem);
    this.totalizar();
  }

  confirmar(value: any) {
  }


  protected criarFormulario() {
    this.exibirProduto = true;
    this.formulario = this.fb.group({
      cliente: this.fb.group({
        id: [],
        nmPessoa: ['']
      }),
      taxaEntrega:['']
    })
  }

  totalItem(item: PedidoItemDTO): number {
    return item.qtItem * item.produto.vlVenda;
  }

  decreaseQuantity(item: PedidoItemDTO) {
    item.qtItem--
    if (item.qtItem == 0) {
      this.removeItem(item);
    }
    this.totalizar();
  }

  increaseQuantity(item: PedidoItemDTO) {
    item.qtItem++
    this.totalizar();
  }

  removeItem(item: PedidoItemDTO) {
    this.pedidoItens = this.pedidoItens.filter((it: PedidoItemDTO) => it.produto.id != item.produto.id)
    this.totalizar();
  }

  totalizar() {
    this.subtotal = 0;
    for (let item of this.pedidoItens) {
      let total = item.qtItem * item.produto.vlVenda;
      this.subtotal += total;
    }
    this.subtotal += this.vlTaxaEntrega;
  }

  selecionarTaxaEntrega(te: any){
    for (let tx of this.taxaEntregas) {
      if (tx.id == te){
        this.vlTaxaEntrega = tx.vlTaxaEntrega;
        this.totalizar();
        break;
      }
    }
  }

  produtofake() {
    let x = { "qtItem": 1, "produto": { "id": 4, "idEmpresa": 1, "nmProduto": "Lazanha a bolonhesa", "vlVenda": 13, "vlCompra": 10, "stProdutoComposto": "N", "status": "A", "categoria": { "id": 2, "nmCategoria": "Refeições", "dsComentario": "refeições saúdave", "status": "A", "exibirConsulta": "S", "urlImagem": null, "statusEnum": "A", "exibirConsultaEnum": "S" }, "urlImagem": null, "unidadeVenda": { "id": 1, "nmUnidadeMedida": "Unidade", "sgUnidadeMedida": "UN", "status": "A", "emAlteracao": false }, "qtAcompanhamento": 2, "qtMinimaEstoque": 10, "dsUrlBlog": "http://blog.paladarfit.com.br/#lasanhafit", "qtEmcada": 100, "emCada": { "id": 4, "nmUnidadeMedida": "Gramas", "sgUnidadeMedida": "GR", "status": "A", "emAlteracao": false }, "nrCodigoBarra": "0314654654654655456", "dsInfoComplementar": "produto de teste", "composicoes": [{ "idProduto": 4, "idProdutoComposicao": 1, "nmProdutoComposicao": "carne moida", "idUnidadeMedida": 4, "nmUnidadeMedida": "GR", "qtUtilizada": 100, "mostrarUnidade": "S" }, { "idProduto": 4, "idProdutoComposicao": 2, "nmProdutoComposicao": "macarrão integral", "idUnidadeMedida": 4, "nmUnidadeMedida": "GR", "qtUtilizada": 300, "mostrarUnidade": "S" }, { "idProduto": 4, "idProdutoComposicao": 3, "nmProdutoComposicao": "tomate organicos", "idUnidadeMedida": 1, "nmUnidadeMedida": "UN", "qtUtilizada": 2, "mostrarUnidade": "N" }], "infoNutricionais": [{ "idProduto": 4, "idInfoNutricional": 1, "dsInfoNutricional": "caboidrato", "idUnidadeMedida": 1, "nmUnidadeMedida": "Unidade", "sgUnidadeMedida": "UN", "qtUtilizada": 10 }, { "idProduto": 4, "idInfoNutricional": 2, "dsInfoNutricional": "gordura", "idUnidadeMedida": 1, "nmUnidadeMedida": "Unidade", "sgUnidadeMedida": "UN", "qtUtilizada": 10 }, { "idProduto": 4, "idInfoNutricional": 3, "dsInfoNutricional": "teste", "idUnidadeMedida": 1, "nmUnidadeMedida": "Unidade", "sgUnidadeMedida": "UN", "qtUtilizada": 10 }] }, "adicionais": [{ "id": 1, "idCategoria": 2, "nmCategoria": "Refeições", "idProduto": 1, "nmProduto": "carne moida", "idUnidadeMedida": 1, "nmUnidadeMedida": "UN", "qtUtilizada": 100, "status": "A", "vlAdicional": 0.33, "selecionado": true }, { "id": 2, "idCategoria": 2, "nmCategoria": "Refeições", "idProduto": 2, "nmProduto": "macarrão integral", "idUnidadeMedida": 4, "nmUnidadeMedida": "GR", "qtUtilizada": 100, "status": "A", "vlAdicional": 0.33, "selecionado": true }] };
    this.pedidoItens.push(x);
  }

  clientefake() {
    let x = { "id": 1, "nmPessoa": "Daniel Rodrigues Aguiar", "dtNascimento": "12/10/1974", "nrCpf": "65847580100", "fone": { "id": 1, "nrFoneResidencial": "(61) 3605-1086", "nrFoneCelular": "61996863636", "nrFoneWhatsapp": "61996863636" }, "usuario": null, "status": "A", "urlImagem": null, "enderecos": [{ "id": 21, "idPessoa": 1, "dsEndereco": "casa 15", "dsLogradouro": "SQ 15", "nrNumero": "12", "dsComplemento": "Casa 92", "dsReferencia": null, "nmBairro": "israel tecidos", "nrCep": "72880576", "nmCidade": "Cidade Ocidental", "ufCidade": "GO" }] };
    this.cliente = x;
    this.enderecos = x.enderecos;
  }

  carregarCombos() {
    this.pagService.findAllAtivo().subscribe(res => this.formaPagmentos = res, error => TrataErrorService.tratarError(error));
    this.taxaService.findAllAtivo().subscribe(res => this.taxaEntregas = res, error => TrataErrorService.tratarError(error));
  }
}



